import React from 'react'
import Center from '../grid/center'
import * as S from './style'

const ItemModal = ({ openModal, setOpenModal, title, color, children }) => (
  <S.Modal open={openModal}>
    <S.ModalBackground onClick={() => setOpenModal(false)} />
    <S.ModalPaper>
      <S.ModalTitle color={color}>
        <Center>
          <S.ModalTitleDisplay>
            <h3>{title}</h3>
            <S.ModalCloseButton onClick={() => setOpenModal(false)}>
              close
            </S.ModalCloseButton>
          </S.ModalTitleDisplay>
        </Center>
      </S.ModalTitle>
      <S.ModalContent>
        <Center>
          <S.ItemBody color={color}>{children}</S.ItemBody>
        </Center>
      </S.ModalContent>
    </S.ModalPaper>
  </S.Modal>
)

export default ItemModal
